
































import Vue from "vue";
import Component from "vue-class-component";
import { AppService } from "../service/app.service";
import CafeCreateBanner from "./cafe/CafeCreateBanner.vue";

@Component({
  components: { CafeCreateBanner },
})
export default class App extends Vue {
  app = AppService.instance;

  cafes = {};

  async mounted(): Promise<void> {
    // ! get this only when user logged in
    // todo backend must check user login
    const res = await this.app.api.request("cafe.mine");
    // console.log("my cafe .. ;", res);
    this.cafes = res;
    // console.log(this.$store);
  }
  logout(): void {
    console.log("App::logout()");
    this.app.api.logout();
  }
}
